import { type ComponentProps } from 'react'

import { type MessageButton } from '@td-org/chat-widget-shared'

import BottomFixedAreaPortal from '../BottomFixedArea/BottomFixedAreaPortal.tsx'
import TextMessage from '../TextMessage/index.ts'

import QuickReplyButtons from './QuickReplyButtons.tsx'

type QuickReplyMessageProps = {
  replies: MessageButton[]
  isLastMessage: boolean
  onButtonClick: (info: MessageButton) => void
} & ComponentProps<typeof TextMessage>

function QuickReplyMessage(props: QuickReplyMessageProps): JSX.Element {
  const { replies, isLastMessage, onButtonClick, ...textMessageProps } = props

  return (
    <>
      <TextMessage {...textMessageProps} />
      {isLastMessage && (
        <BottomFixedAreaPortal>
          <QuickReplyButtons replies={replies} onClick={onButtonClick} />
        </BottomFixedAreaPortal>
      )}
    </>
  )
}

export default QuickReplyMessage
