import { type MessageButton } from '@td-org/chat-widget-shared'

import classes from './QuickReply.module.scss'

type QuickReplyButtonsProps = {
  replies: MessageButton[]
  onClick: (info: MessageButton) => void
}

const QuickReplyButtons = ({ replies, onClick }: QuickReplyButtonsProps): JSX.Element => {
  return (
    <div className={classes.replies}>
      {replies.map((item: MessageButton, index: number) => {
        return (
          <button className={classes.reply} key={index} data-qa='quick_action' onClick={() => onClick(item)}>
            {item.text}
          </button>
        )
      })}
    </div>
  )
}
export default QuickReplyButtons
